.QuipsContainer {
  background: #d1a776
    url(data:image/png;base64,)
    center / 5000px 100px;
  padding-top: 20px;
  position: relative;

  .Limerick-Display-Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 100px;
    min-height: 900px;
    .Drawer {
      position: absolute;
      padding-left: 15px;
      padding-right: 15px;
      min-height: 900px;
      background-image: linear-gradient(
        to bottom,
        #f9ebb7 3%,
        #e4c695 4%,
        #d6a867
      );
      top: 0;
      border-radius: 0 0 20px 20px;
      width: 90%;
      @media screen and (min-width: 700px) {
        width: 630px;
      }
    }
  }

  .drawer {
    background-image: linear-gradient(
      to bottom,
      #f9ebb7 3%,
      #e4c695 4%,
      #d6a867
    );
    box-shadow: 0 0 0 1px #6c4116;
    border-radius: 20px;
    display: inline-block;
    height: 330px;
    position: absolute;
    transition: 0.2s ease-out all;
    z-index: 998;
    bottom: 0px;
    margin-left: -20px;
    width: calc(100% + 10px);
    @media screen and (min-width: 700px) {
      width: 640px;
    }
  }

  .PageTitle-Container {
    background-color: white;
    width: 200px;
    padding: 30px 0;
    margin: auto;
    margin-top: 60px;
    margin-bottom: 20px;
    border: solid 10px #222222;
    h1 {
      margin: 0;
    }
  }

  .handle {
    background-image: linear-gradient(
      tp top,
      #efefef 35%,
      #454545 38%,
      #2c2c2c 75%,
      #efefef 80%
    );
    background-image: linear-gradient(
      to bottom,
      #efefef 35%,
      #454545 38%,
      #2c2c2c 75%,
      #efefef 80%
    );
    border: 1px solid #a3682e;
    border-radius: 60px;
    box-shadow: 0 2px 4px -2px #000;
    width: 200px;
    height: 30px;
    margin: auto;
    z-index: 1;
  }

  .QuipsLoader {
    padding-top: 300px;
  }

  .QuipErrorWrapper {
    padding-top: 300px;
    text-align: center;
  }
}
